<template>
  <div class="blogroll">
    <div class="main">
      <div class="d-title">友情链接</div>
      <div class="content">
        <div class="top display-flex">
          <div class="inner" v-for="(item,index) of blogroll" :key="index">
            <i class="el-icon-s-opportunity"></i>
            <a :href="item.path" target="_blank">{{item.name}}</a>
          </div>
        </div>

        <div class="bottom">
          <div class="d-sTitle">注意点：</div>
          <div class="d-twoBlock"></div>
          <div>
            请确认贵站能正常访问
            <br>
            内容健康
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'BlogRoll',
    data() {
      return {
        blogroll: [
          // {
          //   path: 'http://www.xiaomawang.com',
          //   name: '小码王'
          // },
          {
            path: 'http://www.b2360.com/',
            name: '黔优网'
          },
          {
            path:'https://www.zhyocean.cn/',
            name:'程序猿张先森'
          },
          {
            path: 'https://element.eleme.cn',
            name: 'ElementUI'
          },
          {
            path: 'http://www.wangeditor.com',
            name: 'wangEditor'
          },
          {
            path: 'https://cn.vuejs.org/',
            name: 'Vue'
          },
          {
            path: 'https://spring.io/projects/spring-boot',
            name: 'SpringBoot'
          }
        ]
      }
    }
  }
</script>

<style scoped>
  .content {
    width: 60%;
    margin: 0 auto;
  }

  .top {
    flex-wrap: wrap;
  }

  .top .inner {
    width: 20%;
    text-align: center;
    margin: 10px 0;
  }

  .top .inner a:hover {
    color: #409EFF;
  }

  .bottom>div:nth-child(3) {
    box-sizing: border-box;
    border-left: 4px solid #909399;
    line-height: 24px;
    font-size: 16px;
    background-color: #f9f9f9;
    padding: 10px 0;
    padding-left: 20px;
  }
</style>



